import { configureStore, MiddlewareArray } from "@reduxjs/toolkit";

// Logger Middleware
// action   
import logger from "redux-logger";

// Thunk Middleware
import thunk from 'redux-thunk'

import authReducer from "./features/authSlice";
import boardReducer from "./features/boardSlice";
import tokenReducer from "./features/tokenSlice";
import postItemReducer from "./features/postItemSlice";
import contentReducer from "./features/contentSlice";
import anonymityReducer from "./features/anonymitySlice";

//  1
// const reducer = {
//   auth:authReducer
// }
//
// export const store = configureStore({reducer})

//  2
export const store = configureStore({
  reducer: {
    auth: authReducer,
    content:contentReducer,
    board: boardReducer,
    token: tokenReducer,
    postItem: postItemReducer,
    anonymity: anonymityReducer
  },
  // Middleware
  middleware: new MiddlewareArray().concat(logger).concat(thunk),
  devTools: process.env.NODE_ENV,
});
