import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import fetcher from "../../utils/fetcher-utils";

const name = 'anonymity';

const initialState = {
  anonymityArray:[],
  mailContent:"",
};

export const GET_ANONYMITY = createAsyncThunk(`${name}/GET_ANONYMITY`,async () => {
  const res = await fetcher.get("anonymity");
  return res.data
})


const anonymitySlice = createSlice({
  name,
  initialState,
  reducers:{},
  extraReducers:{
    [GET_ANONYMITY.fulfilled]: (state,action) => {
      state.anonymityArray = action.payload;
    },

  }
})

export default anonymitySlice.reducer;