import React from "react";
import ReactDOM from "react-dom/client";

import "./index.css";

import App from "./App";

import { Route, Routes } from "react-router";
import { BrowserRouter } from "react-router-dom";

import { Provider } from "react-redux";
import { store } from "./store/store";

// Normal Loading
import NotFoundPage from "./component/error/NotFoundPage";

// Lazy Loading
import loadable from "@loadable/component";

const isLoading = { fallback: <div className="text-center">Loading...</div> };

// Main
const LayoutMain = loadable(
  () => import("./component/pages/layout/main/LayoutMain"),
  isLoading
);
const DomesticPage = loadable(
  () => import("./component/pages/main/domestic/DomesticPage"),
  isLoading
);
const AnonymityPage = loadable(
  () => import("./component/pages/main/anonymity/AnonymityPage"),
  isLoading
);

const AdminPage = loadable(
  () => import("./component/pages/main/admin/AdminPage"),
  isLoading
);

// Authentication
const LayoutAuthMain = loadable(
  () => import("./component/pages/layout/auth/LayoutAuthMain"),
  isLoading
);
const RegisterPage = loadable(
  () => import("./component/pages/auth/register/RegisterPage"),
  isLoading
);
const LoginPage = loadable(
  () => import("./component/pages/auth/login/LoginPage"),
  isLoading
);

// Board
const FreeBoardPage = loadable(
  () => import("./component/pages/main/board/freeboard/FreeBoardPage"),
  isLoading
);

const WriteContentPage = loadable(
  () => import("./component/pages/main/board/writecontent/WriteContentPage"),
  isLoading
);

const ViewContentPage = loadable(
  () => import("./component/pages/main/board/viewcontent/ViewContentPage"),
  isLoading
);

const root = ReactDOM.createRoot(document.getElementById("root"));
root.render(
  <Provider store={store}>
    <BrowserRouter>
      {/*ROOT*/}
      <Routes>
        <Route path={""} element={<App />}>
          {/*MAIN*/}
          <Route path="" element={<LayoutMain />}>
            <Route path="" element={<DomesticPage />} />
            <Route path="anonymity" element={<AnonymityPage />} />
            <Route path="admin" element={<AdminPage />} />
            <Route path={"board"}>
              <Route path="" element={<FreeBoardPage />} />
              <Route path="writecontent" element={<WriteContentPage />} />
              <Route path="viewcontent/:postNo" element={<ViewContentPage />} />
            </Route>
          </Route>
        </Route>
        {/*Authentication*/}
        <Route path="auth" element={<LayoutAuthMain />}>
          <Route path="login" element={<LoginPage />}/>
          <Route path="register" element={<RegisterPage />}/>
        </Route>
        {/*ERROR*/}
        <Route path="*" element={<NotFoundPage />}/>
      </Routes>
    </BrowserRouter>
  </Provider>
);
