import React, { useEffect } from "react";
import { useDispatch } from "react-redux";

import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

import { getCookie } from "./utils/cookie-utils";
import { Outlet } from "react-router";
import {
  SET_ISAUTH,
  SET_PROFILE,
  SET_REFRESH,
} from "./store/features/authSlice";
import { SET_TOKEN } from "./store/features/tokenSlice";

function App() {
  const dispatch = useDispatch();
  const accessTokenAsCookies = getCookie("ACCESS_TOKEN");
  const refreshTokenAsCookies = getCookie("REFRESH_TOKEN");

  useEffect(() => {
    if (accessTokenAsCookies) {
      dispatch(SET_REFRESH(accessTokenAsCookies));
      dispatch(SET_TOKEN({ accessTokenAsCookies, refreshTokenAsCookies }));
      dispatch(SET_PROFILE(accessTokenAsCookies));
      //    
      dispatch(SET_ISAUTH(true));
    }
  }, []);

  return (
    <div className="App">
      <Outlet />
      <ToastContainer
        position="bottom-center"
        autoClose={2000}
        hideProgressBar
        newestOnTop
        closeOnClick
        rtl={false}
        pauseOnFocusLoss={false}
        draggable
        pauseOnHover={false}
      />
    </div>
  );
}

export default App;
