import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import fetcher from "../../utils/fetcher-utils";
import { useNavigate } from "react-router";

const name = "content";

const initialState = {
  postTitle: "",
  postContent: "",
  isLoading: false
};
/**
 *   
 * @type {AsyncThunk<any, void, {}>}
 */
export const GET_CONTENT = createAsyncThunk(`${name}/GET_CONTENT`, async (params) => {
  const res = await fetcher.get(`board/${params}`);
  return res.data;
});
/**
 *   
 * TODO :   
 * @type {AsyncThunk<any, void, {}>}
 */
export const POST_CONTENT = createAsyncThunk(
  `${name}/POST_CONTENT`,
  async (data) => {
    const res = await fetcher.post("board", data);
    return res.data;
  }
);


const contentSlice = createSlice({
  name,
  initialState,
  reducers: {
  },
  extraReducers: {
    [GET_CONTENT.fulfilled]: (state, action) => {
      state.boardList = action.payload;
      state.isLoading = false;
    },
    [GET_CONTENT.pending]: (state, action) => {
      state.isLoading = true;
    },
    [GET_CONTENT.rejected]: (state, action) => {
      state.isLoading = false;
    },
    [POST_CONTENT.fulfilled]: (state, action) => {
      state.isLoading = false;
    },
    [POST_CONTENT.pending]: (state, action) => {
      state.isLoading = true;
    },
    [POST_CONTENT.rejected]: (state, action) => {
      state.isLoading = false;
    }
  }
});

export default contentSlice.reducer;
