import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  accessToken: "",
  refreshToken: "",
};

const tokenSlice = createSlice({
  name: "token",
  initialState,
  reducers: {
    SET_TOKEN(state, action) {
      console.log(action.payload);
        state.accessToken = action.payload.accessTokenAsCookies;
        state.refreshToken = action.payload.refreshTokenAsCookies;
    },
    RESET_TOKENSLICE(state) {
      Object.assign(state,initialState)
    },
  },
});

export default tokenSlice.reducer;
export const { SET_TOKEN, RESET_TOKENSLICE } = tokenSlice.actions;
