import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import fetcher from "../../utils/fetcher-utils";
import { toast } from "react-toastify";

const name = "postItem";

const initialState = {
	postNo:"",
	postTitle: "",
	postContent: "",
	postCreateDt:"",
	postModifyDt:"",
	userDto:[],
	postComments:[],
	isLoading: false,
};

/**
 *   
 * @type {AsyncThunk<any, void, {}>}
 */
export const GET_POSTITEM = createAsyncThunk(`${name}/GET_POSTITEM`, async (params) => {
	const res = await fetcher.get(`board/${params}`);
	return res.data;
});
/**
 *  
 * @type {AsyncThunk<any, void, {}>}
 */
export const GET_COMMENT = createAsyncThunk(`${name}/GET_COMMENT`, async (postNo) => {
	const res = await fetcher.get(`api/comments/getAllByPost/${postNo}`);
	return res.data;
});
/**
 *  
 * @type {AsyncThunk<any, void, {}>}
 */
export const POST_COMMENT = createAsyncThunk(`${name}/POST_COMMENT`, async (data) => {
	console.log(data);
	const res = await fetcher.post(`api/comments/create`,data);
	return res.data;
});


const postItemSlice = createSlice({
	name,
	initialState,
	reducers: {
		RESET_POSTITEMSLICE(state){
			Object.assign(state,initialState)
		}
	},
	extraReducers: {
		/**
		 *   
		 * @param state
		 * @param action
		 */
		[GET_POSTITEM.fulfilled]: (state, action) => {
			state.postNo = action.payload.postNo;
			state.postTitle = action.payload.postTitle;
			state.postContent = action.payload.postContent;
			state.postCreateDt = action.payload.postCreateDt;
			state.postModifyDt = action.payload.postModifyDt;
			state.userDto = action.payload.userDto;
			state.isLoading = false;
		},
		[GET_POSTITEM.pending]: (state, action) => {
			state.isLoading = true;
		},
		[GET_POSTITEM.rejected]: (state, action) => {
			state.isLoading = false;
		},
		/**
		 *  
		 * @param state
		 * @param action
		 */
		[GET_COMMENT.fulfilled]: (state, action) => {
			state.postComments = action.payload
			state.isLoading = false;
		},
		[GET_COMMENT.pending]: (state, action) => {
			state.isLoading = true;
		},
		[GET_COMMENT.rejected]: (state, action) => {
			state.isLoading = false;
		},

		/**
		 *  
		 * @param state
		 * @param action
		 */
		[POST_COMMENT.fulfilled]: (state, action) => {
			toast.success("  .")
			state.isLoading = false;
		},
		[POST_COMMENT.pending]: (state, action) => {
			state.isLoading = true;
		},
		[POST_COMMENT.rejected]: (state, action) => {
			state.isLoading = false;
		}
	}}
);

export default postItemSlice.reducer;
export const {RESET_POSTITEMSLICE} =  postItemSlice.actions;
