import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import fetcher from "../../utils/fetcher-utils";
import { removeCookie } from "../../utils/cookie-utils";
import jwtDecode from "jwt-decode";
const name = "board";
const initialState = {
  id: "",
  roles: [],
  isLoading: false,
  isAuth: false,
};

export const SET_LOGIN = createAsyncThunk(`${name}/SET_LOGIN`, async (data) => {
  const res = await fetcher.post("api/login", data);
  return res.data;
});

export const SET_REGISTER = createAsyncThunk(
  `${name}/SET_REGISTER`,
  async (data) => {
    const res = await fetcher.post("api/users/register", data);
    return res.data;
  }
);

export const SET_LOGOUT = createAsyncThunk(`${name}/SET_LOGOUT`, async () => {
  const res = await fetcher.post("api/token/remove-refresh");
  return res.data;
});

export const SET_REFRESH = createAsyncThunk(
  `${name}/SET_REFRESH`,
  async (data) => {
    const res = await fetcher.get("api/token/refresh", {
      headers:{ Authorization: `Bearer ${data}` }
    });
    return res.data;
  }
);

const authSlice = createSlice({
  name,
  initialState,
  reducers: {
    SET_ISAUTH(state, action) {
      state.isAuth = action.payload;
    },
    SET_PROFILE(state, action) {
      const decodeToken = jwtDecode(action.payload);
      state.id = decodeToken.sub;
      state.roles = decodeToken.roles;
    },
    RESET_AUTHSLICE(state) {
      Object.assign(state, initialState);
    },
  },
  extraReducers: {
    [SET_LOGIN.fulfilled]: (state, action) => {
      const decodeToken = jwtDecode(action.payload.access_token);
      state.id = decodeToken.sub;
      state.roles = decodeToken.roles;
      state.isAuth = true;
      state.isLoading = false;
    },

    [SET_LOGIN.pending]: (state, action) => {
      state.isLoading = true;
    },
    [SET_LOGIN.rejected]: (state, action) => {
      state.isLoading = false;
    },
    [SET_LOGOUT.fulfilled]: (state) => {
      removeCookie("ACCESS_TOKEN");
      removeCookie("REFRESH_TOKEN");
      state.isLoading = false;
    },
    [SET_LOGOUT.pending]: (state, action) => {
      state.isLoading = true;
    },
    [SET_LOGOUT.rejected]: (state) => {
      removeCookie("ACCESS_TOKEN");
      removeCookie("REFRESH_TOKEN");
      state.isLoading = false;
    },
    [SET_REGISTER.fulfilled]: (state, action) => {
      state.isLoading = false;
    },

    [SET_REGISTER.pending]: (state, action) => {
      state.isLoading = true;
    },
    [SET_REGISTER.rejected]: (state, action) => {
      state.isLoading = false;
    },
  },
});

export default authSlice.reducer;
export const { SET_ISAUTH, RESET_AUTHSLICE, SET_PROFILE } = authSlice.actions;
