import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import fetcher from "../../utils/fetcher-utils";

const name = "board";

const initialState = {
  boardList: [],
  postTitle: "",
  postContent: "",
  isLoading: false,
};

/**
 *   
 * @type {AsyncThunk<any, void, {}>}
 */
export const GET_BOARD = createAsyncThunk(`${name}/GET_BOARD`, async () => {
  const res = await fetcher.get("board");
  return res.data;
});

const boardSlice = createSlice({
  name,
  initialState,
  reducers: {
    SET_TITLE(state, action) {
      console.log(action);
      state.postTitle = action.payload;
    },
    SET_CONTENT(state, action) {
      state.postContent = action.payload;
    },
    RESET_BOARDSLICE(state) {
      Object.assign(state, initialState)
    },
  },
  extraReducers: {
    [GET_BOARD.fulfilled]: (state, action) => {
      state.boardList = action.payload;
      state.isLoading = false;
    },
    [GET_BOARD.pending]: (state, action) => {
      state.isLoading = true;
    },
    [GET_BOARD.rejected]: (state, action) => {
      state.isLoading = false;
    },
  },
});

export default boardSlice.reducer;
export const { SET_TITLE, SET_CONTENT, RESET_BOARDSLICE } = boardSlice.actions;
